// app/layout.tsx
import "./globals.css";
import { Poppins } from "next/font/google";
import Header from "./components/Header";
import { LanguageProvider } from "./context/LanguageContext";
import { ThemeProvider } from "./context/ThemeContext";   // ‚Üê make sure this import is correct

const poppins = Poppins({
  subsets: ["latin"],
  weight: ["300", "400", "500", "600", "700", "800"],
  variable: "--font-poppins",
  display: "swap",
});

export const metadata = {
  title: "Sibiya Funeral Services",
  description: "Compassionate funeral services across KZN and Johannesburg",
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en" suppressHydrationWarning>
      <body className={`${poppins.variable} font-sans antialiased`}>
        <ThemeProvider>
          <LanguageProvider>
            <Header />
            <main className="pt-0">{children}</main>  {/* no extra padding needed */}
          </LanguageProvider>
        </ThemeProvider>
      </body>
    </html>
  );
}